.blog-list {
    position: absolute;
    top: 300px;
    left: 0;
    right: 0;
    bottom: 0px;
    width: calc(100vw - 20px);
    padding-left: 10px;
    padding-right: 10px;
    box-shadow: var(--box-shadow-active);
    overflow: auto;
}

.blog-list-item {
    width: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    height: 160px;
    box-shadow: var(--box-shadow);
    border-radius: var(--border-radius);
    cursor: pointer;
}

.blog-list-item-img {
    width: 100%;
    height: 100px;
    line-height: 100px;
    float: left;
    background-size: 100% 100% !important;
    border-radius: var(--border-radius) var(--border-radius) 0 0;
}

.blog-list-item-title-img {
    width: 100%;
    height: 100px;
    line-height: 100px;
    text-align: center;
    font-size: 30px;
    float: left;
    border-radius: var(--border-radius) var(--border-radius) 0 0;
    color: white;
}

.blog-list-item-text {
    width: calc(100% - 20px);
    height: 40px;
    padding: 10px;
    background: var(--main-bg-color-0);
    float: left;
    border-radius: 0 0 var(--border-radius) var(--border-radius);
}

.blog-list-item-title {
    width: 100%;
    line-height: 20px;
    float: left;
    font-size: 20px;
    font-weight: bold;
    display: flex;
}

.blog-list-item-title .left {
    width: 50%;
}

.blog-list-item-title .right {
    width: 50%;
    text-align: right;
    font-size: 10px;
    color: var(--primary-color);
}


.blog-list-item-subtitle {
    width: 100%;
    height: 20px;
    line-height: 20px;
    float: left;
}

.blog-user-card {
    width: 100vw;
    height: 300px;
    background-size: cover !important;
    box-shadow: var(--box-shadow);
    color: white;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}

.blog-user-avatar {
    width: 100vw;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
}


.blog-user-avatar-img {
    width: 150px;
    height: 150px;
    border-radius: 150px;
    box-shadow: var(--box-shadow);
}

.blog-user-info {
    width: 100vw;
    height: 100px;
    line-height: 100px;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
}

.blog-detail {
    padding-left: 10px;
    padding-right: 10px;
}

.blog-detail-title {
    height: 40px;
    line-height: 40px;
    font-size: 40px;
    font-weight: bold;
    margin: 20px 0 20px 0;
}

.blog-detail-img {
    width: 100%;
    height: 300px;
}

.blog-detail-img img {
    width: 100%;
    height: 100%;
    float: left;
    background-size: cover !important;
}

.blog-detail-text-img {
    width: 100%;
    height: 300px;
    line-height: 300px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    color: white;
}

ol li {
    word-break: break-all;
}

.blog-book-list {
    margin-top: 50px;
    float: left;
}

.blog-question-title {
    height: 45px;
    line-height: 45px;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
}

.blog-question-content {
    height: calc(100% - 45px);
    width: calc(100vw - 20px);
    overflow: auto;
    padding-left: 10px;
    padding-right: 10px;
    background: var(--main-bg-color-1);
}

.bq-operate {
    display: flex;
}

.bq-o-left {
    height: 10vh;
    width: calc(40vw - 10px);
    display: flex;
    justify-items: center;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
}

.bq-o-left .icon {
    margin: 10px;
    font-size: 30px;
}

.bq-o-right {
    width: 60vw;
    height: 10vh;
    display: flex;
    justify-items: center;
    align-items: center;
}

