/*layout*/
.header {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 45px;
    line-height: 45px;
    width: 100vw;
    display: -webkit-flex; /* Safari */
    display: flex;
    z-index: 100;
}

.header.active {
    /*backdrop-filter: var(--main-filter);*/
    box-shadow: var(--box-shadow);
    background: var(--main-bg-color-0);
}

.header-nav-left {
    width: calc(33.3% - 5px);
    text-align: left;
    padding-left: 5px;
    cursor: pointer;
    font-size: 30px;
    /*color: var(--primary-color);*/
    font-weight: bold;
}

.header-nav-mid {
    width: 33.3%;
    text-align: center;
    font-weight: bold;
    font-size: large;
    font-weight: bold;
}

.header-nav-right {
    width: calc(33.3% - 5px);
    text-align: right;
    padding-right: 5px;
    font-size: 30px;
    cursor: pointer;
    /*color: var(--primary-color);*/
    font-weight: bold;
}

.main {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.top-main {
    position: absolute;
    top: 45px;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.bottom-main {
    position: absolute;
    top: 0;
    bottom: 10vh;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.top-bottom-main {
    position: absolute;
    top: 45px;
    bottom: 10vh;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.sys-main-title {
    margin-top: 45px;
    font-size: 30px;
}

.footer {
    position: fixed;
    bottom: 0px;
    right: 0;
    left: 0;
    width: 100vw;
    height: 10vh;
    box-shadow: var(--box-shadow);
    background: var(--main-bg-color-0);
}

.nav-container {
    width: calc(100% - 20px);
    height: 100%;
    display: flex;
    justify-items: center;
    align-items: center;
    flex-direction: row;
    padding-left: 15px;
    padding-right: 15px;
}

.sys-footer-btn {
    font-size: 25px;
    font-weight: bold;
    width: 100%;
    height: 10vh;
    line-height: 10vh;
    color: var(--primary-color);
    text-align: center;
}

/*button*/
.sys-btn {
    height: 50px;
    width: calc(100% - 20px);
    line-height: 50px;
    margin: 10px;
    border-radius: var(--border-radius);
    background: var(--main-bg-color-0);
    color: var(--primary-color);
    font-weight: bold;
    text-align: center;
    cursor: pointer;
    box-shadow: var(--box-shadow);
}

.sys-pri-btn {
    height: 50px;
    width: calc(100% - 20px);
    line-height: 50px;
    margin: 10px;
    border-radius: var(--border-radius);
    background: var(--primary-color);
    color: white;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
}

.sys-icon {
    /*background: var(--main-bg-color-1);*/
    /*border-radius: 30px;*/
    font-size: 35px;
    /*padding: 5px;*/
    cursor: pointer;
}

.sys-loading {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    flex-direction: column;
}

.sys-loading-icon {
    animation: ac-circle 1s infinite;
    -webkit-animation: ac-circle 1s infinite;
}

.fullSwiper {
    width: 100vw;
    height: 100vh;
}

.sys-mask {
    position: fixed;
    height: 100vh;
    width: 100vw;
}

.sys-popup {
    z-index: 100;
    height: 50vh;
    width: 100vw;
    background: var(--main-mask-bg);
    backdrop-filter: var(--main-filter);
    position: fixed;
    bottom: 0;
    z-index: 100;
}

.sys-popup-foot {
    position: fixed;
    bottom: 0px;
    backdrop-filter: var(--main-filter);
    width: 100vw;
    height: 10vh;
    line-height: 10vh;
    text-align: center;
}

.sys-popup-content {
    height: 100%;
    width: 100%;
    float: left;
    overflow: auto;
}

.sys-popup-icon {
    background: var(--main-bg-color-0);
    box-shadow: var(--box-shadow);
    border-radius: var(--border-radius);
    font-size: 30px;
    height: 100px;
    line-height: 100px;
    margin: 10px;
    width: calc(50vw - 20px);
    text-align: center;
}

.sys-menu-page-head {
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: right;
    font-size: 25px;
    background: var(--main-mask-bg);
    backdrop-filter: var(--main-filter);
}

.sys-menu-page-head .icon {
    margin: 5px;
}

.sys-input {
    outline-style: none;
    padding: 10px;
    width: calc(100vw - 40px);
    margin: 10px;
    height: 30px;
    font-size: 20px;
}

.search-head {
    position: fixed;
    top: 0px;
    backdrop-filter: var(--main-filter);
    width: 100vw;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
}

.search-content {
    padding-top: 10vh;
    padding-bottom: 10vh;
    width: 100vw;
    float: left;
    overflow: auto;
}

.search-item {
    width: calc(100vw - 40px);
    height: 80px;
    line-height: 80px;
    text-align: center;
    font-size: 20px;
    background: var(--main-bg-color-0);
    margin: 20px;
    border-radius: var(--border-radius);
    box-shadow: var(--box-shadow);
}


@keyframes ac-circle {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

hr {
    color: var(--main-text-color-1);
}